.admin{display:grid;grid-template-columns:150px 1fr;grid-column-gap:10px;-webkit-column-gap:10px;column-gap:10px;height:100%}

main{grid-area:main;display:flex;flex-direction:column}main section{padding:2vh 1vw 2vh}@media (max-width: 767px){main section{padding:2vh 2vw 2vh}}

.contacts{display:flex;height:100%;flex-direction:row;width:100%;grid-column-gap:10px;-webkit-column-gap:10px;column-gap:10px;padding:0 10px;justify-content:space-around;font-weight:300}.contacts .numbers{display:flex;flex-direction:column;justify-content:center;align-items:flex-start}.contacts .signature{display:flex;flex-direction:column;justify-content:center}.contacts .signature .logotype{display:flex;font-family:parisienne;font-size:200%;color:#2fd6d0;line-height:normal}.contacts .signature .copyright{display:flex;line-height:normal;justify-content:center}@media (max-width: 767px){.copyright{font-size:50%}}@media (max-width: 1279px){.copyright{font-size:70%}}

footer{grid-area:footer;display:flex;align-items:center;justify-content:center;background:#585C67;color:#fff;font-weight:bold}

